<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>Webbutik med VueJS</h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Detta projekt r en E-handelsapplikation som r byggd med Vue.js fr frontend, vilket mjliggr en
                    dynamisk och responsiv anvndarupplevelse. Fr backend anvnde jag C#
                    fr att hantera serverlogik och datahantering. Jag anvnde mig av Vue Router fr att skapa olika
                    sidor i applikationen. Utver det s anvnde jag mig av tv projekt i Visual Studio, ett fr backend
                    och ett fr frontend. Drefter kopplade jag ihop dem genom att lgga till en referens i
                    frontend-projektet till backend-projektet.
                </p>
                <p>
                    <strong>
                        Funktionalitet:<br />
                    </strong>

                    <strong>Startsida:</strong> Visar en versikt av produkter med bild, namn och pris.<br />
                    <strong>Produktdetaljer:</strong> Varje produkt r klickbar och leder till en dedikerad sida med mer
                    information.<br />
                    <strong>Simulerad Kp-process:</strong> Beskaren kan klicka p en "Kp"-knapp, vilket tar dem till
                    ett bestllningsformulr dr den valda produkten visas. Formulret ger intrycket av att kunna
                    slutfra ett kp genom att fylla i betalningsuppgifter.
                </p>
                <p>
                    <strong>Viktig anmrkning:</strong> Kp-funktionen r simulerad; den r inte kopplad till en
                    fungerande betalningslsning, bank eller leverantr. Formulret fungerar endast som en mockup fr
                    att visa processen.
                </p>
            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ProjectList from '@/components/ProjectList.vue';
import imageShopIndJs from '@/assets/images/WebShop/ShopIndJs.png';
import imageShopApp from '@/assets/images/WebShop/ShopApp.png';
import imageShopImg from '@/assets/images/WebShop/ShopImg.png';
import imageShopHome from '@/assets/images/WebShop/ShopHome.png';
import imageShopProd from '@/assets/images/WebShop/ShopProd.png';
import imageShopCheck from '@/assets/images/WebShop/ShopCheck.png';
import imageShopChSc from '@/assets/images/WebShop/ShopChSc.png';
import imageShopStart from '@/assets/images/WebShop/ShopStart.png';
import imageShopOne from '@/assets/images/WebShop/ShopOne.png';
import imageShopEnd from '@/assets/images/WebShop/ShopEnd.png';

export default {
    name: 'WebShopView',
    components: {
        ProjectList
    },

    data() {
        const allImages = [
            { id: 1, url: imageShopIndJs, description: 'Koden fr index.js.' },
            { id: 2, url: imageShopApp, description: 'Koden fr App.vue.' },
            { id: 3, url: imageShopImg, description: 'Koden fr bilderna.' },
            { id: 4, url: imageShopHome, description: 'Koden fr Home-komponenten.' },
            { id: 5, url: imageShopProd, description: 'Koden fr Product-komponenten.' },
            { id: 6, url: imageShopCheck, description: 'Koden fr Checkout-komponenten.' },
            { id: 7, url: imageShopChSc, description: 'Koden fr Checkout-scriptet.' },
            { id: 8, url: imageShopStart, description: 'Resultat: Startsidan.' },
            { id: 9, url: imageShopOne, description: 'Resultat: Ett specifikt produkt.' },
            { id: 10, url: imageShopEnd, description: 'Resultat: Kassan, betalfunktionen r inte inkodad.' },
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            return this.images.filter(image => image.id >= 1 && image.id <= 7);
        },
        resultImages() {
            return this.images.filter(image => image.id >= 8 && image.id <= 10);
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>