        <div class="content-container">
            <div class="home-2">
                <h1>
                    FizzBuzz
                </h1>
                <h2>
                    Beskrivning
                </h2>
                <p>
                    Detta projekt r ett konsolprogram i C# som lser den klassiska FizzBuzz-utmaningen.<br />
                    Applikationen gr igenom siffrorna 1 till 100 och tillmpar en serie villkorlig logik.
                </p>
                <p>
                    Syftet med detta projekt var att frstrka min frstelse fr grundlggande programmeringskoncept.
                    <br />
                    Genom att anvnda C# visade jag hur man effektivt hanterar upprepning och logiska fldeskontroller.
                    Resultatet r en stabil, snabb applikation som utgr ett utmrkt bevis p frmgan att
                    lsa en vanlig intervjufrga inom programmering. Projekt visar ven grundlggande kunskaper inom C#,
                    inklusive anvndning av loopar (for-loop), villkorssatser (if/else-if/else), och aritmetiska
                    operationer (modulo) fr att uppn en dynamisk och logisk utskrift. Det r ett tydligt exempel p
                    problemlsning och en ren kodstruktur.
                </p>
                <p>
                    <strong>
                        Huvudfunktioner fr programmet
                    </strong>
                </p>
                <p>
                    <strong>
                        Upprepning och utskrift:
                    </strong>
                    <br />
                    Programmet loopar genom tal frn 1 till 100 och skriver ut varje tal p en
                    ny rad.
                </p>
                <p>
                    <strong>
                        Villkorlig logik:
