<script>
// @ is an alias to /src
import ProjectList from '@/components/ProjectList.vue';
import imageFizzBuzz from '@/assets/images/FizzBuzz.png';
import imageFizzR from '@/assets/images/FizzR.png';

export default {
    name: 'FizzBuzzView',
    components: {
        ProjectList
    },

    data() {
        // Definiera alla bilder frst
        const allImages = [
