        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            // Hr vljer vi bild 1 (som r koden i din setup)
            return this.images.filter(image => image.id === 1);
        },
        resultImages() {
            // Hr vljer vi bild 2 (som r resultatet i din setup)
            return this.images.filter(image => image.id === 2);
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>