<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Portfolio i MVC
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Projektet r en Portfolio-webbplats byggd med ramverket ASP.NET MVC (Model-View-Controller)
                    i sprket C#. Detta val ger en stabil, skalbar och strukturerad backend. Sidan anvnder MongoDB fr
                    att lagra all projektdata och innehll. Detta mjliggr snabb och flexibel hantering av
                    informationen. Webbplatsen r uppdelad i sektioner:<br />

                    - En startsida som fungerar som en introduktion och presentation av mig.<br />
                    - En separat "Projekt"-flik som visar ett urval av mina bsta arbeten.<br />
                    <br />
                    Anvndaren kan navigera till projektversikten och sedan klicka p ett specifikt projekt fr att se
                    dess funktionalitet och detaljer. De projekt som presenteras r primrt de som r utvecklade med
                    webb MVC. Fr lagring och hantering av projektdatan valdes MongoDB, vilket utnyttjades som
                    databaslsning.
                    <br />
                    Projekten som ligger i detta portfolio kommer att presenteras under en egen flik.
                </p>
            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ProjectList from '@/components/ProjectList.vue';
import image1 from '@/assets/images/Portf/PortfMvcHC.png';
import image2 from '@/assets/images/Portf/PortfMvsIn.png';
import image3 from '@/assets/images/Portf/PortfMvcPC.png';
import image4 from '@/assets/images/Portf/PortfMvcPI.png';
import image5 from '@/assets/images/Portf/PortfStart.png';
import image6 from '@/assets/images/Portf/PortfProj.png';

export default {
    name: 'PortfolioMvcView',
    components: {
        ProjectList
    },

    data() {
        const allImages = [
            { id: 1, url: image1, description: 'HomeController - styr vyn; hr visar den att Index-vyn ska innehlla en lista ver ml.' },
            { id: 2, url: image2, description: 'Koden fr Index-vyn (startsidan) definierar presentationen av mig och mina ml.' },
            { id: 3, url: image3, description: 'ProjektController -  styr vyn; vilket i detta projekt, behvde vara en index view.' },
            { id: 4, url: image4, description: 'Projekt Index: Startsida med kortfattade projektbeskrivningar och lnkar till dess funktioner.' },
            { id: 5, url: image5, description: 'Startsidan: Hr r resultatet av koden i HomeController och tillhrande Index.' },
            { id: 6, url: image6, description: 'Projekt Index: startsidan fr projekten. Hr r resultatet av ProjectControllern med tillhrande Index.' }
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            const codeIds = [1, 2, 3, 4];
            return this.images.filter(image => codeIds.includes(image.id));
        },
        resultImages() {
            const resultIds = [5, 6];
            return this.images.filter(image => resultIds.includes(image.id));
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>