<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Mlsttning
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Detta projekt r ett webbaserat MVC-program (Model-View-Controller) utvecklat i C#. Projektet
                    demonstrerar grundlggande MVC-arkitektur genom att hantera data som rr anvndarml.
                    Applikationen r
                    byggd med en MongoDB-databas som backend fr att lagra och hmta information.
                </p>
                <p>
                    <strong>
                        Databasanslutning (Class Library)
                    </strong><br />
                    Fr att hantera databaskommunikationen skapades en separat klass kallad "Databas.cs" som en del
                    av ett Class Library. Denna design mjliggr en renare struktur genom att separera datalogik frn
                    huvudapplikationen. Anslutningen mellan huvudprojektet och databasbiblioteket etablerades genom
                    en ProjectReference.
                </p>
                <p>
                    <strong>
                        Funktionalitet i GoalController
                    </strong><br />
                    GoalController hanterar logiken fr anvndarml och r uppdelad i tv huvudvyer:<br />

                    <strong> Visuell vy:</strong> Anvndaren kan se en komplett lista ver alla befintliga ml.
                    <br />

                    <strong>Skapa ml:</strong> En dedikerad vy dr anvndaren kan fylla i information fr att skapa
                    ett nytt ml. Nr ett
                    ml har skapats, lggs det automatiskt till i listan.<br />

                    Dessutom har GoalController en direkt Delete-funktion som tar bort ett ml utan att krva en
                    separat vy. Denna implementation visar en frstelse fr bde visuell interaktion och
                    databasoperationer.
                </p>
            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import ProjectList from '@/components/ProjectList.vue';
import portfGoalHC from '@/assets/images/Portf/PortfGoalHC.png';
import portfGoalIn from '@/assets/images/Portf/PortfGoalIn.png';
import portfGoalCr from '@/assets/images/Portf/PortfGoalCr.png';
import portfDBConn from '@/assets/images/Portf/PortfDBConn.png';
import modelGoal from '@/assets/images/Portf/ModelGoal.png';
import goalIn from '@/assets/images/Portf/GoalIn.png';
import goalCreate from '@/assets/images/Portf/GoalCreate.png';

export default {
    name: 'GoalView',
    components: {
        ProjectList
    },

    data() {
        // Alla bilder definierade hr
        const allImages = [
            // Kodbilder (ID 1, 3, 4, 5)
            { id: 1, url: portfGoalHC, description: 'Kod: GoalControllern. Den bestmmer vad vyerna ska innehlla.' },
            { id: 2, url: portfGoalCr, description: 'Kod: Koden fr vy:n fr att skapa en mlsttning. Dr man fr lgga till ett datum, om det r startdatum eller slutdatum r upp till en sjlv.' },
            { id: 3, url: portfDBConn, description: 'Kod: Kopplingen till Databasen i projektfilen.' },
            { id: 4, url: modelGoal, description: 'Kod: En klass som ligger i Database.cs. Vilket bestmmer vad som ska tas emot och vad som visas. Den ska stmma verens med de namnen som finns i databasen.' },
            { id: 5, url: portfGoalIn, description: 'Resultat: Index: startsidan fr mlsttningssidan. Hr visas alla mlsttningar och om man vill skapa eller radera en mlsttning.' },
            // Resultatbilder (ID 2, 6, 7)            
            { id: 6, url: goalIn, description: 'Resultat: Startsidan fr mlsttningssidan som terfinns i "portfolio i webb MVC". Det finns ingen separat sida fr att radera, utan det dyker upp en ruta som frgar om man r sker p om man vill radera mlsttningen. Mlen man lgger till hamnar ven p startsidan fr portfoliot.' },
            { id: 7, url: goalCreate, description: 'Resultat: Sidan fr att skapa en ny mlsttning. Hr ser man hur man skapar en mlsttning. Efter man skapat en mlsttning, hamnar man p startsidan fr mlsttningssidan' }
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            const codeIds = [1, 2, 3, 4, 5];
            return this.images.filter(image => codeIds.includes(image.id));
        },
        resultImages() {
            const resultIds = [6, 7];
            return this.images.filter(image => resultIds.includes(image.id));
        }
    },
    mounted() {
        // activeImage r redan satt i data(), s mounted r inte ndvndig fr detta lngre.
        // Du kan ta bort den om du inte har annan logik hr.
    },
    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>