<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Bloggplattform med Frontend och Backend i VueJS
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Detta projekt r en fullstack-lsning fr en bloggplattform, dr jag har kombinerat frontend med
                    backend. Fr att ge en verblick p kod innehllet i sidan, har varje del en kort frklaring fr vad
                    delen gr i projektet.
                </p>
                <p>
                    <strong>Frontend:</strong> Utvecklad med Vue.js fr att ge en dynamisk och responsiv
                    anvndarupplevelse.
                    <br />
                    <strong>Backend:</strong> Skapad i C# och fungerar som ett API som hanterar datafldet. Denna del
                    kommunicerar med en SQL-databas dr all information fr bloggplattformen lagras.
                    <br />
                    <strong>Funktionalitet:</strong> Anvndare kan utfra CRUD-operationer (Create, Read, Update,
                    Delete) p blogginlgg.
                    <br />
                    <strong>Visa:</strong> Hmta en lista ver alla inlgg eller vlja ett specifikt inlgg fr att
                    lsa.
                    <br />
                    <strong>Skapa:</strong> Anvndaren kan skapa inlgg genom ett formulr som finns p sidan.
                    <br />
                    <strong>Redigera:</strong> Uppdatera befintliga inlgg p sidan.
                    <br />
                    <strong>Ta bort:</strong> Radera inlgg frn sidan och databasen.
                    <br />
                    <strong>Databas:</strong> All data lagras i en SQL-databas, vilket skerstller att informationen r
                    persistent och lttillgnglig.
                </p>
                <p>
                    Detta projekt visar min frmga att bygga en komplett webbapplikation med bde frontend och backend,
                    samt hantera data p ett effektivt stt.
                </p>                
            </div>
            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ProjectList from '@/components/ProjectList.vue';
import imageBloggApiCon from '@/assets/images/BloggFB/BloggApiCon.png';
import imageBloggApiDB from '@/assets/images/BloggFB/BloggApiDB.png';
import imageBloggApiCl from '@/assets/images/BloggFB/BloggApiCl.png';
import imageBloggApiPro from '@/assets/images/BloggFB/BloggApiPro.png';
import imageBloggVIn from '@/assets/images/BloggFB/BloggVIn.png';
import imageBloggVApp from '@/assets/images/BloggFB/BloggVApp.png';
import imageBloggVHome from '@/assets/images/BloggFB/BloggVHome.png';
import imageBloggVOne from '@/assets/images/BloggFB/BloggVOne.png';
import imageBloggVList from '@/assets/images/BloggFB/BloggVList.png';
import imageBloggStart from '@/assets/images/BloggFB/BloggStart.png';
import imageBloggSingle from '@/assets/images/BloggFB/BloggSingle.png';
import imageBloggOm from '@/assets/images/BloggFB/BloggOm.png';


export default {
    name: 'BloggView',
    components: {
        ProjectList
    },

    data() {
        const allImages = [
            { id: 1, url: imageBloggApiCon, description: 'Koden fr API Controller.' },
            { id: 2, url: imageBloggApiDB, description: 'Koden fr API databaskoppling.' },
            { id: 3, url: imageBloggApiCl, description: 'Koden fr API Class.' },
            { id: 4, url: imageBloggApiPro, description: 'Koden fr API Program.' },
            { id: 5, url: imageBloggVIn, description: 'Koden fr Vue index.' },
            { id: 6, url: imageBloggVApp, description: 'Koden fr Vue App.' },
            { id: 7, url: imageBloggVHome, description: 'Koden fr Vue Home.' },
            { id: 8, url: imageBloggVOne, description: 'Koden fr Vue SingleBlog.' },
            { id: 9, url: imageBloggVList, description: 'Koden fr Vue BlogList.' },
            { id: 10, url: imageBloggStart, description: 'Resultat: Startsidan.' },
            { id: 11, url: imageBloggSingle, description: 'Resultat: Singleblog-sidan.' },
            { id: 12, url: imageBloggOm, description: 'Resultat: Om oss-sidan.' },
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            return this.images.filter(image => image.id >= 1 && image.id <= 9);
        },
        resultImages() {
            return this.images.filter(image => image.id >= 10 && image.id <= 12);
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>