<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Trningsdagbok: C# MVC-applikation med MongoDB
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Projektet r en webbaserad Trningsdagbok utvecklad med C# och ASP.NET MVC. Applikationen tillter
                    anvndaren att registrera och hantera sin trning. <br />
                    Jag anvnde mig av Visual Studio fr att skapa projektet och MongoDB fr att hantera databasen.
                    Fr att interagera med MongoDB anvnde jag MongoDBs officiella C#-driver.
                </p>
                <p>
                    Anvndare kan skapa, lsa, uppdatera och ta bort (CRUD) trningspass och vningar. Varje
                    trningspass
                    innehller information som datum, tid och en lista ver vningar med detaljer som namn, antal set,
                    reps
                    och vikt. Applikationen anvnder MongoDB som databas fr att lagra trningsdata, vilket mjliggr
                    flexibel och skalbar lagring. Grnssnittet r anvndarvnligt och responsivt, vilket gr det enkelt
                    fr
                    anvndare att navigera och interagera med sina trningsdata. Projektet demonstrerar grundlggande
                    webbutvecklingskoncept som MVC-arkitektur, routing, formulrhantering och databasinteraktion med
                    MongoDB.
                </p>
                <p>
                    <strong>Huvudfunktioner:</strong>
                    <br />
                    - <strong>vningar:</strong> Anvndare kan skapa, lsa, uppdatera och ta bort vningar.
                    <br />
                    - <strong>Trningstillfllen:</strong> Anvndare kan registrera ett pass genom att ange datum, vlja
                    en
                    vning och specificera resultatet (t.ex. lyft vikt eller sprungna kilometer).<br />
                    - <strong>CRUD-operationer:</strong> Fullstndig CRUD-funktionalitet fr bde vningar och
                    trningstillfllen. <br />
                    - <strong>Databasinteraktion:</strong> Applikationen anvnder MongoDB fr att lagra och hmta
                    trningsdata.
                </p>
            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import ProjectList from '@/components/ProjectList.vue';
import imageWorkoutHome from '@/assets/images/Workout/WorkoutHome.png';
import imageWorkoutDiary from '@/assets/images/Workout/WorkoutDiary.png';
import imageWorkoutCr from '@/assets/images/Workout/WorkoutCr.png';
import imageWorkoutDel from '@/assets/images/Workout/WorkoutDel.png';
import imageWorkoutExIn from '@/assets/images/Workout/WorkoutExIn.png';
import imageWorkouteExCr from '@/assets/images/Workout/WorkoutExCr.png'; // Notera att denna har 'e' i 'Workoute'
import imageWorkoutController from '@/assets/images/Workout/WorkoutController.png';
import imageWorkoutCreate from '@/assets/images/Workout/WorkoutCreate.png';
import imageWorkoutIn from '@/assets/images/Workout/WorkoutIn.png';
import imageWorkoutClass from '@/assets/images/Workout/WorkoutClass.png';
import imageWorkoutDB from '@/assets/images/Workout/WorkoutDB.png';
import imageWorkoutExCl from '@/assets/images/Workout/WorkoutExCl.png';
import imageWorkoutExCont from '@/assets/images/Workout/WorkoutExCont.png';
import imageWorkoutExCreate from '@/assets/images/Workout/WorkoutExCreate.png'; // Notera att denna har 'Create' istllet fr 'Cre'
import imageWorkoutExIndex from '@/assets/images/Workout/WorkoutExIndex.png';

export default {
    name: 'WorkoutView',
    components: {
        ProjectList
    },

    data() {
        // Definiera alla bilder frst
        const allImages = [
            // Kodbilder (ID 1-9)
            { id: 1, url: imageWorkoutController, description: 'Koden fr Workout Controller.' },
            { id: 2, url: imageWorkoutCreate, description: 'Koden fr funktionen fr att skapa ett trningspass (Create Workout).' },
            { id: 3, url: imageWorkoutIn, description: 'Koden fr funktionen som listar trningspass (Workout Index).' },
            { id: 4, url: imageWorkoutClass, description: 'Koden fr klassmodellen Workout.' },
            { id: 5, url: imageWorkoutDB, description: 'Koden fr databasklassen som interagerar med MongoDB.' },
            { id: 6, url: imageWorkoutExCont, description: 'Koden fr Exercise Controller.' },
            { id: 7, url: imageWorkoutExCreate, description: 'Koden fr funktionen fr att skapa en vning (Create Exercise).' },
            { id: 8, url: imageWorkoutExIndex, description: 'Koden fr funktionen som listar vningar (Exercise Index).' },
            { id: 9, url: imageWorkoutClass, description: 'Koden fr klassmodellen Workout.' },
            { id: 10, url: imageWorkoutExCl, description: 'Koden fr klassmodellen Exercise.' },
            // Resultatbilder (ID 10-15)
            { id: 11, url: imageWorkoutHome, description: 'Resultat: Startsida som visar en versikt.' },
            { id: 12, url: imageWorkoutDiary, description: 'Resultat: "Workout Diary"  en lista ver trningspass.' },
            { id: 13, url: imageWorkoutCr, description: 'Resultat: "Create Workout"  sidan fr att skapa ett nytt trningspass.' },
            { id: 14, url: imageWorkoutDel, description: 'Resultat: "Delete Workout"  bekrftelse p borttagning av ett trningspass.' },
            { id: 15, url: imageWorkoutExIn, description: 'Resultat: "Exercises Index" - en lista ver alla vningar.' },
            { id: 16, url: imageWorkouteExCr, description: 'Resultat: "Create Exercise"  sidan fr att skapa en ny vning.' },
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            return this.images.filter(image => image.id >= 1 && image.id <= 10);
        },
        resultImages() {
            return this.images.filter(image => image.id >= 11 && image.id <= 16);
        }
    },
    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>