<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Bloggplattform med SQL-databas
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    I detta projekt byggde jag en bloggplattform frn grunden med en SQL-databas som backend.<br />
                    Jag fokuserade p att skapa en effektiv lsning fr att hantera anvndargenererat innehll. Beskare
                    kan smidigt skapa nya inlgg via ett formulr, dr innehll som titel, sammanfattning och brdtext
                    sparas skert och strukturerat i databasen. P plattformens startsida presenteras alla inlgg i en
                    versiktlig vy, och det r enkelt att navigera till fullstndiga inlgg genom ett enkelt klick.

                    P sidan fr varje inlgg finns funktioner fr att redigera eller radera inlgget, vilket ger
                    anvndarna full kontroll ver sitt innehll. Genom att anvnda SQL-databasen skerstllde jag att
                    all data hanteras p ett plitligt stt, med std fr grundlggande CRUD-operationer (Create, Read,
                    Update, Delete).
                </p>
                <p>
                    De frsta tre bilderna under rubriken "kod" visar SQL-databasen med tabeller och data.
                    De fljande nio bilderna under samma rubrik visar koden fr sjlva projektet.
                    De bilderna som ligger under rubriken "resultat" visar resultatet av koden som byggde projektet.
                </p>

            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ProjectList from '@/components/ProjectList.vue';
import imageSqlBloggDBId from '@/assets/images/SqlBlogg/SqlBloggDBId.png';
import imageSqlBloggDBpo from '@/assets/images/SqlBlogg/SqlBloggDBpo.png';
import imageSqlBloggDBtb from '@/assets/images/SqlBlogg/SqlBloggDBtb.png';
import imageSqlBloggSol from '@/assets/images/SqlBlogg/SqlBloggSol.png';
import imageSqlBloggCon from '@/assets/images/SqlBlogg/SqlBloggCon.png';
import imageSqlBloggDBcl from '@/assets/images/SqlBlogg/SqlBloggDBcl.png';
import imageSqlBloggPcl from '@/assets/images/SqlBlogg/SqlBloggPcl.png';
import imageSqlBloggHC from '@/assets/images/SqlBlogg/SqlBloggHC.png';
import imageSqlBloggIn from '@/assets/images/SqlBlogg/SqlBloggIn.png';
import imageSqlBloggDet from '@/assets/images/SqlBlogg/SqlBloggDet.png';
import imageSqlBloggCre from '@/assets/images/SqlBlogg/SqlBloggCre.png';
import imageSqlBloggStart from '@/assets/images/SqlBlogg/SqlBloggStart.png';
import imageSqlBloggDetails from '@/assets/images/SqlBlogg/SqlBloggDetails.png';
import imageSqlBloggCreate from '@/assets/images/SqlBlogg/SqlBloggCreate.png';


export default {
    name: 'SqlBlogglistView',
    components: {
        ProjectList
    },

    data() {
        const allImages = [
            { id: 1, url: imageSqlBloggDBId, description: 'SQL-databasen fr blogginlggen (post ID).' },
            { id: 2, url: imageSqlBloggDBpo, description: 'SQL-databasen fr blogginlggen (posts).' },
            { id: 3, url: imageSqlBloggDBtb, description: 'SQL-databasen fr blogginlggen (diagram).' },
            { id: 4, url: imageSqlBloggSol, description: 'Tv projekt i Solution..' },
            { id: 5, url: imageSqlBloggCon, description: 'Koden fr databasanslutningen (Connection).' },
            { id: 6, url: imageSqlBloggDBcl, description: 'Koden fr databasklassen, med koppling till SQL-Databasen samt berttar fr databasen om ett inlgg visas, skapas, uppdateras eller raderas (DBClass).' },
            { id: 7, url: imageSqlBloggPcl, description: 'Koden fr Post Class.' },
            { id: 8, url: imageSqlBloggHC, description: 'Koden fr Home Controller.' },
            { id: 9, url: imageSqlBloggIn, description: 'Koden fr Home Index.' },
            { id: 10, url: imageSqlBloggDet, description: 'Koden fr Home Details.' },
            { id: 11, url: imageSqlBloggCre, description: 'Koden fr att skapa inlgg (Create).' },
            { id: 12, url: imageSqlBloggStart, description: 'Resultat: Startsidan med lista ver inlgg (Start).' },
            { id: 13, url: imageSqlBloggDetails, description: 'Resultat: Detaljerad vy ver ett blogginlgg (Details).' },
            { id: 14, url: imageSqlBloggCreate, description: 'Resultat: Sidan fr att skapa ett nytt inlgg (Create).' },
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            return this.images.filter(image => image.id >= 1 && image.id <= 11);
        },
        resultImages() {
            return this.images.filter(image => image.id >= 12 && image.id <= 14);
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>