<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Receptlista
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Detta projekt r en utveckling av en receptapplikation (C# MVC) dr jag implementerade en
                    fullstack-lsning fr en receptwebbplats med hjlp av ASP.NET MVC i C#. Projektet skapades och
                    hanterades i Visual Studio 2022. Applikationen har tv huvuddelar:<br />
                    <strong>Publik vy:</strong> En startsida som presenterar alla tillgngliga recept i en lista.<br />
                    <strong>Administration:</strong> En dedikerad adminpanel dr en administratr kan logga in och
                    hantera innehllet, inklusive att skapa nya recept och lgga till dess ingredienser.<br />
                    Som databaslsning valde jag MongoDB fr att effektivt lagra och hantera den dynamiska receptdatan
                    som matas in via adminpanelen.
                </p>
                <p>
                    Applikationen r designad fr att vara anvndarvnlig och responsiv, vilket skerstller en smidig
                    upplevelse fr bde slutanvndare och administratrer. Jag lade stor vikt vid att skapa en intuitiv
                    navigationsstruktur och ett rent grnssnitt fr att underltta anvndningen av webbplatsen.
                </p>
                <p>
                    Projekt gav mig vrdefull erfarenhet av att arbeta med fullstack-utveckling, inklusive
                    backend-logik, frontend-design och databashantering. Jag lrde mig ocks vikten av att skapa
                    anvndarvnliga grnssnitt fr bde slutanvndare och administratrer. Jag r njd med resultatet av
                    detta projekt och ser fram emot att fortstta utveckla mina frdigheter inom webbutveckling och
                    utforska fler teknologier i framtida projekt.
                </p>
            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import ProjectList from '@/components/ProjectList.vue';
import imageReceptSol from '@/assets/images/Receptsida/ReceptSol.png';
import imageReceptDBCon from '@/assets/images/Receptsida/ReceptDBCon.png';
import imageReceptHomeC from '@/assets/images/Receptsida/ReceptHomeC.png';
import imageReceptHomeIn from '@/assets/images/Receptsida/ReceptHomeIn.png';
import imageReceptHomeDe from '@/assets/images/Receptsida/ReceptHomeDe.png';
import imageReceptDB from '@/assets/images/Receptsida/ReceptDB.png';
import imageReceptReCl from '@/assets/images/Receptsida/ReceptReCl.png';
import imageReceptIngCl from '@/assets/images/Receptsida/ReceptIngCl.png';
import imageReceptAdmin from '@/assets/images/Receptsida/ReceptAdmin.png';
import imageReceptStart from '@/assets/images/Receptsida/ReceptStart.png';
import imageReceptDe from '@/assets/images/Receptsida/ReceptDe.png';
import imageReceptAdminR from '@/assets/images/Receptsida/ReceptAdminR.png';


export default {
    name: 'ReceptListaView',
    components: {
        ProjectList
    },

    data() {
        const allImages = [
            { id: 1, url: imageReceptSol, description: 'Tre projekt i Solution.' },
            { id: 2, url: imageReceptDBCon, description: 'Koden fr databaskopplingen (DB Connection).' },
            { id: 3, url: imageReceptHomeC, description: 'Koden fr Home Controller.' },
            { id: 4, url: imageReceptHomeIn, description: 'Koden fr Home Index.' },
            { id: 5, url: imageReceptHomeDe, description: 'Koden fr Home Details.' },
            { id: 6, url: imageReceptDB, description: 'Koden fr databasklassen, med koppling till MongoDB (DB).' },
            { id: 7, url: imageReceptReCl, description: 'Koden fr Recipe Class.' },
            { id: 8, url: imageReceptIngCl, description: 'Koden fr Ingredient Class.' },
            { id: 9, url: imageReceptAdmin, description: 'Koden fr adminpanelen, dr man skapar recept.' },
            { id: 10, url: imageReceptStart, description: 'Resultat: Startsidan (Start).' },
            { id: 11, url: imageReceptDe, description: 'Resultat: Detaljerad vy ver ett recept (Details).' },
            { id: 12, url: imageReceptAdminR, description: 'Resultat: Adminpanelen fr att skapa recept (Admin Recipes).' },
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            return this.images.filter(image => image.id >= 1 && image.id <= 9);
        },
        resultImages() {
            return this.images.filter(image => image.id >= 10 && image.id <= 12);
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>