<template>
    <div class="main-layout-2">
        <ProjectList />

        <div class="content-container">
            <div class="home-2">
                <h1>
                    Trningsdagbok med SQL
                </h1>

                <h2>
                    Beskrivning
                </h2>
                <p>
                    Jag skapade en responsiv webbapplikation fr trningsloggning med hjlp av C# och ASP.NET MVC.
                    Applikationen tillter anvndare att skapa, lsa, redigera och ta bort trningspass. Varje pass
                    lagrar detaljer som vning (max 50 tecken), antal set, reps och vikt.
                </p>
                <p>
                    Fr datalagring anvnde jag en SQL Server-databas med en tabell som heter "Exercises". Denna tabell
                    har kolumner fr ID (primrnyckel), vning, set, reps och vikt. Jag implementerade databasanslutning
                    och CRUD-operationer. Applikationen har en enkel och anvndarvnlig design med HTML och CSS,
                    inklusive responsiv layout fr olika skrmstorlekar. Denna trningsdagbok med SQL r ett praktiskt
                    verktyg fr att spra och hantera trningspass, vilket hjlper anvndare att hlla koll p sina
                    framsteg och ml.
                    <br />
                    Jag testade applikationen noggrant fr att skerstlla att alla funktioner fungerade korrekt och att
                    data sparades och hmtades frn databasen som frvntat.
                </p>
                <p>
                    <strong>
                        Teknisk implementation:
                    </strong>
                    <br />
                    - <strong>Databas:</strong> En SQL Server-databas skapades (via SSMS) med en tabell fr att lagra
                    trningstillfllena.<br />
                    - <strong>Arkitektur:</strong> Koden delades upp i tv Visual Studio-projekt: ett fr
                    MVC-webbplatsen
                    och ett separat datalagerprojekt fr databashantering (inklusive CRUD-metoder). <br />
                    - <strong>Grnssnitt:</strong> HomeController hanterar logiken, och vyerna byggdes med Razor-syntax.
                    Bootstrap anvndes fr en anvndarvnlig och responsiv design.
                </p>
            </div>

            <div class="home-3">
                <h2>
                    Bilder frn projektet
                </h2>
                <div v-if="activeImage" class="main-image-container">
                    <img :src="activeImage.url" :alt="activeImage.description" class="main-image">
                    <p>{{ activeImage.description }}</p>
                </div>

                <h3>Klicka p bilderna nedan fr att se koden och resultatet</h3>

                <h2>Kod</h2>
                <div class="thumbnail-container">
                    <div v-for="image in codeImages" :key="image.id" @click="selectImage(image)" class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>

                <h2>Resultat</h2>
                <div class="thumbnail-container">
                    <div v-for="image in resultImages" :key="image.id" @click="selectImage(image)"
                        class="thumbnail-item">
                        <img :src="image.url" :alt="image.description" class="thumbnail-image"
                            :class="{ 'active-thumbnail': activeImage && image.id === activeImage.id }">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import ProjectList from '@/components/ProjectList.vue';
import imageSqlPracticeDBId from '@/assets/images/SqlWorkout/SqlPracticeDBId.png';
import imageSqlPracticeDB from '@/assets/images/SqlWorkout/SqlPracticeDB.png';
import imageSqlPracticeDiagram from '@/assets/images/SqlWorkout/SqlPracticeDiagram.png';
import imageSqlPracticeDcs from '@/assets/images/SqlWorkout/SqlPracticeDcs.png';
import imageSqlPracticeCL from '@/assets/images/SqlWorkout/SqlPracticeCL.png';
import imageSqlPracticeHC from '@/assets/images/SqlWorkout/SqlPracticeHC.png';
import imageSqlPracticeIn from '@/assets/images/SqlWorkout/SqlPracticeIn.png';
import imageSqlPracticeCr from '@/assets/images/SqlWorkout/SqlPracticeCr.png';
import imageSqlPracticeDet from '@/assets/images/SqlWorkout/SqlPracticeDet.png';
import imageSqlPracticeEd from '@/assets/images/SqlWorkout/SqlPracticeEd.png';
import imageSqlPracticeDe from '@/assets/images/SqlWorkout/SqlPracticeDe.png';
import imageSqlPracticeStart from '@/assets/images/SqlWorkout/SqlPracticeStart.png';
import imageSqlPracticeSkapa from '@/assets/images/SqlWorkout/SqlPracticeSkapa.png';
import imageSqlPracticeDeta from '@/assets/images/SqlWorkout/SqlPracticeDeta.png';
import imageSqlPracticeRe from '@/assets/images/SqlWorkout/SqlPracticeRe.png';
import imageSqlPracticeRad from '@/assets/images/SqlWorkout/SqlPracticeRad.png';

export default {
    name: 'SqlWorkoutView',
    components: {
        ProjectList
    },

    data() {
        // Definiera alla bilder frst
        const allImages = [
            { id: 1, url: imageSqlPracticeDBId, description: 'versikt ver SQL databasen.' },
            { id: 2, url: imageSqlPracticeDB, description: 'SQL databasen med tabeller.' },
            { id: 3, url: imageSqlPracticeDiagram, description: 'Diagram ver SQL databasen.' },
            { id: 4, url: imageSqlPracticeDcs, description: 'Koden fr databaskopplingen samt berttar fr databasen om en vning ska visas, skapas, uppdateras eller raderas (DB Connection).' },
            { id: 5, url: imageSqlPracticeCL, description: 'Koden fr vnings klassen.' },
            { id: 6, url: imageSqlPracticeHC, description: 'Koden fr Home Controller.' },
            { id: 7, url: imageSqlPracticeIn, description: 'Koden fr Home Index.' },
            { id: 8, url: imageSqlPracticeCr, description: 'Koden fr Create.' },
            { id: 9, url: imageSqlPracticeDet, description: 'Koden fr Details.' },
            { id: 10, url: imageSqlPracticeEd, description: 'Koden fr Edit.' },
            { id: 11, url: imageSqlPracticeDe, description: 'Koden fr Delete.' },
            { id: 12, url: imageSqlPracticeStart, description: 'Resultat: Startsidan (Start).' },
            { id: 13, url: imageSqlPracticeSkapa, description: 'Resultat: Skapa en vning.' },
            { id: 14, url: imageSqlPracticeDeta, description: 'Resultat: Detaljerad vy ver en vning.' },
            { id: 15, url: imageSqlPracticeRe, description: 'Resultat: Redigera en vning.' },
            { id: 16, url: imageSqlPracticeRad, description: 'Resultat: Radera en vning.' },
        ];

        return {
            activeImage: allImages[0] || null,
            images: allImages
        };
    },
    computed: {
        codeImages() {
            return this.images.filter(image => image.id >= 1 && image.id <= 11);
        },
        resultImages() {
            return this.images.filter(image => image.id >= 12 && image.id <= 16);
        }
    },

    methods: {
        selectImage(image) {
            this.activeImage = image;
        }
    }
}
</script>